.modalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(97, 97, 97, 0.6);
  backdrop-filter: blur(4px);

  z-index: 9999;
}

@media screen and (min-width: 768px) {
  .modalOverlay {
    background: rgba(43, 43, 43, 0.6);
    backdrop-filter: none;
  }
}

.modalContainer {
  width: 280px;
  position: relative;
  display: flex;
  flex-direction: column;

  padding: 12px 12px 60px 12px;

  border-radius: 20px;
  background: var(--white-color);
}

.modalTitle {
  font-size: 24px;
  color: var(--text-color);
  text-align: center;
  font-family: Manrope;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  letter-spacing: 0.96px;
  margin-bottom: 20px;
}

@media screen and (min-width: 768px) {
.modalTitle {
  margin-bottom: 40px;
  font-size: 36px;
  letter-spacing: 1.44px;
  }
}

.modalContainerBtn {
  width: 240px;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (min-width: 768px) {
  .modalContainerBtn {
  display: flex;
  margin: 0px;
  width: 560px;
  gap: 20px;
  justify-content: center;
  }
}

.modalInfo {
  width: 240px;
  color: var(--text-color);
  text-align: center;
  font-family: Manrope;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: 0.56px;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 40px;
}
@media screen and (min-width: 768px) {
  .modalInfo {
  width: 528px;
  font-size: 20px;
  line-height: 28px;
  letter-spacing: 0.8px;
  }
}

@media screen and (min-width: 768px) {
  .modalContainer {
    width: 608px;
    
    padding: 24px 24px 60px 24px;

    border-radius: 40px;
    box-shadow: 3px 8px 14px 0px rgba(136, 198, 253, 0.19);
  }
}

.modalCrossBtn {
  background-color: transparent;
  border: none;
  cursor: pointer;
  padding: 0px;
  height: 24px;
  width: 24px;
}

.modalHeader {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.modalBody {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}

.modalText {
  padding-top: 20px;
  color: var(--text-color);
  font-size: 24px;
  font-weight: 500;
  letter-spacing: 0.96px;
}

@media screen and (min-width: 768px) {
  .modalText {
    text-align: center;
    padding-top: 12px;
  }
}

.modalCancelBtn {
  display: flex;
  width: 100%;
  height: 40px;
  padding: 8px 20px;
  justify-content: center;
  align-items: center;

  border-radius: 40px;
  border: 2px solid var(--blue-color);
  background: var(--white-color);
  cursor: pointer;

  color: var(--blue-color);
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 0.64px;
}

.modalCancelBtn:hover {
  background: var(--gradient-blue);
  color: var(--white-color);
  border: none;
}

.modalYesBtn {
  display: flex;
  width: 100%;
  height: 40px;
  padding: 8px 20px;
  justify-content: center;
  align-items: center;
  gap: 8px;

  border-radius: 40px;
  background: var(--blue-color);

  border: none;
  color: var(--background-color);
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 0.64px;
}

.modalYesBtn:hover,
.modalYesBtn :focus {
  background: var(--gradient-blue);
}

.modalActions {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 0px 0px;
  width: 100%;
  align-items: center;
}

@media screen and (min-width: 768px) {
  .modalActions {
    display: flex;
    flex-direction: row;
    gap: 17px;
    padding: 0 142px;
    align-items: center;
  }
}

/* Button */

.loginBtn {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 240px;
  height: 40px;
  padding: 8px 20px;

  gap: 8px;
  margin-bottom: 12px;

  border-radius: 40px;
  border: 2px solid var(--yellow-color);
  background: var(--yellow-color);

  font-size: 16px;
  font-weight: 700;
  letter-spacing: 0.64px;
  color: var(--background-color);
  fill: var(--background-color);
  cursor: pointer;
}

@media screen and (min-width: 768px) {
.loginBtn {
    width: 165px;
    margin-bottom: 0px;
  }
}

.loginBtn:hover,
.loginBtn:focus {
  background: linear-gradient(90deg,
      #ffde3f 0,
      #ffd02a 25%,
      #ffc107 50%,
      #f6b200 75%,
      #efa400 100%);
  border: none;
}

.registerBtn {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 240px;
  height: 40px;
  padding: 7px 20px;

  border-radius: 40px;
  border: 2px solid var(--yellow-color);

  background-color: var(--background-color);

  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0.64px;
  color: var(--yellow-color);

  cursor: pointer;
}

@media screen and (min-width: 768px) {
  .registerBtn {
    width: 142px;
  }
}

.registerBtn:hover,
.registerBtn:focus {
  background-color: var(--yellow-color);
  color: var(--background-color);
}